.content {
	position: fixed;
	width: 100%;
	top: 0;
	left: 0px;
	transition: all .3s ease;
	height: 100%;
	max-height: 65px;
	z-index: 100;
}
/* NAVBAR */
.content nav {
    background: linear-gradient(120deg, var(--themeColor01) 0%, var(--themeColor02) 100%);
	height: 64px;
	padding: 0 20px;
	display: flex;
	align-items: center;
	grid-gap: 28px;
	position: sticky;
	top: 0;
	left: 0;
	box-shadow: 5px 5px 5px #c2bef7;
}
.logo img {
	position: absolute;
	left: 15px;
	top: 2px;
	width: 45px;
	height: 45px;
	cursor: pointer;
}
.logoText{
	position: absolute;
	left: 10px;
	top: 43px;
	color: #fff;
	font-size: 12px;
	margin-left: 10px;
	cursor: pointer;
}

.content nav form {
	max-width: 400px;
	width: 100%;
	margin-right: auto;
}
.content nav .formGroup {
	position: relative;
	padding-left: 50px;
}
.content nav .formGroup input {
	width: 100%;
	background: #fff;
	border-radius: 5px;
	border: none;
	outline: none;
	padding: 10px 36px 10px 16px;
	transition: all .3s ease;
}
.content nav .formGroup input:focus {
	box-shadow: 0 0 0 1px #1775F1, 0 0 0 4px #D0E4FF;
}
.content nav .formGroup .icon {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 16px;
	color: #8D8D8D;
}
.content nav .navlink {
	position: relative;
}
.content nav .navlink .icon {
	font-size: 18px;
	color: #fff;
	cursor: pointer;
	transition: all .3s ease;
}
.content nav .navlink .badge {
	position: absolute;
	top: -12px;
	right: -12px;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	border: 2px solid #fff;
	background: #FC3B56;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 10px;
	font-weight: 700;
}
.content nav .divider {
	width: 1px;
	background: #fff;
	height: 12px;
	display: block;
}
.content nav .profile {
	position: relative;
}
.content nav .profile .icon {
	font-size: 40px;
	cursor: pointer;
	color: #fff;
}

.dropdownMenu {
  position: absolute;
  right: 0;
  top: 40px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  list-style: none;
  padding: 0;
  margin: 0;
  min-width: 140px;
  z-index: 100;
}

.dropdownMenu li {
  padding: 10px 16px;
  cursor: pointer;
  transition: background 0.2s;
}

.dropdownMenu li:hover {
  background: #f3f3f3;
}

@media (max-width: 600px) {
    .content {
        transform: scale(0.8);
        transform-origin: top left;
		width: 125%;
		height: 125%;
    }
}

/* Tema Escuro */
@media (prefers-color-scheme: dark) {
  .content nav {
    background: linear-gradient(120deg, #23243a 0%, #1a1b2e 100%);
    box-shadow: 5px 5px 5px #11101a;
  }
  .content nav .navlink .icon,
  .content nav .profile .icon,
  .logoText {
    color: #e0e0e0;
  }
  .dropdownMenu {
    background: #23243a;
    color: #e0e0e0;
    border: 1px solid #444;
  }
  .dropdownMenu li:hover {
	color: #000;
	}
}
